<template>
  <div class="card mb-3">
    <div class="row no-gutters">
      <div class="col-md-4">
        <img
          :src="user.image"
          width="300px"
          height="300px"
        >
      </div>
      <div class="col-md-8">
        <div class="card-body">
          <h5 class="card-title">
            {{ user.name }}
          </h5>
          <p class="card-text">
            {{ user.email }}
          </p>
          <ul class="list-unstyled list-inline">
            <li><strong>{{ user.commentsLength }}</strong> 已評論餐廳</li>
            <li><strong>{{ user.favoritedRestaurantsLength }}</strong> 收藏的餐廳</li>
            <li><strong>{{ user.followingsLength }}</strong> followings (追蹤者)</li>
            <li><strong>{{ user.followersLength }}</strong> followers (追隨者)</li>
          </ul>
          <template v-if="currentUser.id === user.id">
            <router-link
              :to="{ name: 'user-edit', params: { id: user.id } }"
              class="btn btn-primary"
            >
              Edit
            </router-link>
          </template>
          <template v-else>
            <button
              v-if="isFollowed"
              type="button"
              class="btn btn-danger"
              @click.stop.prevent="unfollow(user.id)"
            >
              取消追蹤
            </button>
            <button
              v-else
              type="button"
              class="btn btn-primary"
              @click.stop.prevent="follow(user.id)"
            >
              追蹤
            </button>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import usersAPI from './../apis/users'
import { Toast } from './../utils/helpers'
import { mapState } from 'vuex'

export default {
  props: {
    user: {
      type: Object,
      required: true
    },
    isFollowed: {
      type: Boolean,
      required: true
    }
  },
  computed: {
    ...mapState(['currentUser'])
  },
  methods: {
    async follow (userId) {
      try {
        const { data } = await usersAPI.addFollowing({ userId })

        if (data.status !== 'success') {
          throw new Error(data.message)
        }

        this.$emit('after-follow', this.currentUser)
        Toast.fire({
          icon: 'success',
          title: '成功跟隨用戶'
        })
      } catch (error) {
        Toast.fire({
          icon: 'error',
          title: '無法跟隨用戶，請稍後再試'
        })
      }
    },
    async unfollow (userId) {
      try {
        const { data } = await usersAPI.deleteFollowing({ userId })

        if (data.status !== 'success') {
          throw new Error(data.message)
        }

        this.$emit('after-unfollow', this.currentUser)
        Toast.fire({
          icon: 'success',
          title: '成功取消跟隨用戶'
        })
      } catch (error) {
        Toast.fire({
          icon: 'error',
          title: '無法取消跟隨用戶，請稍後再試'
        })
      }
    }
  }
}
</script>
